'use client'

import { useEffect, useState, useMemo, useCallback } from 'react'
import Link from 'next/link'
import { useDebounce } from '@/hooks/useDebounce'

interface Business {
  id: string
  name: string
  tags?: string[]
  lat?: number
  lon?: number
}

// Funchal coordinates
const FUNCHAL_LAT = 32.6497
const FUNCHAL_LON = -16.9082
const RADIUS_KM = 30

// API Key - should be moved to environment variable
const BTCMAP_API_KEY = process.env.NEXT_PUBLIC_BTCMAP_API_KEY

// Fallback data in case API fails
const fallbackBusinesses: Business[] = [
  {
    id: '1',
    name: 'Bitcoin Beach Madeira',
    tags: ['beach', 'bar', 'restaurant']
  },
  {
    id: '2',
    name: 'Digital Nomad Hub',
    tags: ['coworking', 'community']
  }
]

// Calculate distance between two points using Haversine formula
function calculateDistance(lat1: number, lon1: number, lat2: number, lon2: number): number {
  const R = 6371 // Earth's radius in km
  const dLat = (lat2 - lat1) * Math.PI / 180
  const dLon = (lon2 - lon1) * Math.PI / 180
  const a = 
    Math.sin(dLat/2) * Math.sin(dLat/2) +
    Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) * 
    Math.sin(dLon/2) * Math.sin(dLon/2)
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a))
  return R * c
}

export function BitcoinMap() {
  const [isMounted, setIsMounted] = useState(false)
  const [businesses, setBusinesses] = useState<Business[]>([])
  const [loading, setLoading] = useState(true)
  const [searchQuery, setSearchQuery] = useState('')
  const [error, setError] = useState<string | null>(null)
  const debouncedSearchQuery = useDebounce(searchQuery, 300)

  // Memoized fetch function
  const fetchBusinesses = useCallback(async () => {
    try {
      if (!BTCMAP_API_KEY) {
        throw new Error('BTCMap API key not configured')
      }

      const controller = new AbortController()
      const timeoutId = setTimeout(() => controller.abort(), 10000) // 10s timeout

      const response = await fetch('https://api.btcmap.org/v2/venues', {
        headers: {
          'Authorization': `Bearer ${BTCMAP_API_KEY}`,
          'Accept': 'application/json'
        },
        signal: controller.signal
      })

      clearTimeout(timeoutId)

      if (!response.ok) {
        const errorText = await response.text()
        console.error('API Response:', errorText)
        throw new Error(`Failed to fetch venues: ${response.status} - ${errorText}`)
      }
      
      const data = await response.json()
      
      // Transform and filter the data
      const simplifiedData = data
        .map((business: any) => ({
          id: business.id,
          name: business.name,
          tags: business.tags || [],
          lat: business.lat,
          lon: business.lon
        }))
        .filter((business: Business) => {
          if (!business.lat || !business.lon) return false
          const distance = calculateDistance(
            FUNCHAL_LAT, 
            FUNCHAL_LON, 
            business.lat, 
            business.lon
          )
          return distance <= RADIUS_KM
        })
      
      setBusinesses(simplifiedData)
      setError(null)
    } catch (err) {
      console.error('Error fetching businesses:', err)
      setError('Failed to load businesses. Please try again later.')
      setBusinesses(fallbackBusinesses)
    } finally {
      setLoading(false)
    }
  }, [])

  useEffect(() => {
    setIsMounted(true)
    fetchBusinesses()
  }, [fetchBusinesses])

  // Memoized filtered businesses
  const filteredBusinesses = useMemo(() => {
    if (!debouncedSearchQuery) return businesses
    const query = debouncedSearchQuery.toLowerCase()
    return businesses.filter(business => 
      business.name.toLowerCase().includes(query) ||
      business.tags?.some(tag => tag.toLowerCase().includes(query))
    )
  }, [businesses, debouncedSearchQuery])

  if (!isMounted) {
    return (
      <div className="w-full h-[300px] bg-ocean/10 rounded-lg animate-pulse flex items-center justify-center">
        <span className="text-forest/60">Loading map...</span>
      </div>
    )
  }

  return (
    <div className="space-y-8 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      {/* Map Widget */}
      <div className="w-full h-[400px] rounded-lg overflow-hidden border border-sand/20 shadow-lg">
        <div className="w-full h-full bg-ocean/5 flex items-center justify-center">
          <div className="text-center space-y-2">
            <div className="text-bitcoin text-2xl" aria-hidden="true">üó∫Ô∏è</div>
            <p className="text-forest/60">Interactive map coming soon</p>
            <Link 
              href="https://btcmap.org/community/free-madeira"
              target="_blank"
              rel="noopener noreferrer"
              className="text-sm text-bitcoin hover:text-bitcoin/80 inline-block"
            >
              View on BTCMap.org ‚Üí
            </Link>
          </div>
        </div>
      </div>

      {/* Search Section */}
      <section className="space-y-4" aria-labelledby="search-heading">
        <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center gap-2">
          <h3 id="search-heading" className="text-lg font-semibold text-sand">Search Bitcoin-Accepting Businesses</h3>
        </div>
        
        {/* Search Bar */}
        <div className="relative">
          <input
            type="text"
            placeholder="Search Bitcoin-accepting businesses..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="w-full px-4 py-2 rounded-lg border border-sand/20 bg-white/5 text-sand placeholder-forest/40 focus:outline-none focus:ring-2 focus:ring-bitcoin/20"
            aria-label="Search businesses"
          />
        </div>

        {/* Results count and error message */}
        <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center gap-2">
          <div className="text-sm text-forest/60">
            Found {filteredBusinesses.length} business{filteredBusinesses.length !== 1 ? 'es' : ''} within {RADIUS_KM}km of Funchal
          </div>
          {error && (
            <div className="text-sm text-bitcoin" role="alert">
              {error}
            </div>
          )}
        </div>
      </section>
    </div>
  )
} 